import React from "react";
import { connect } from "react-redux";
import { NavLink } from "react-router-dom";
import { fetchAllquizsets } from "../../state/actions/user.action";

import "../styles/Quiz.css";

class Quizset extends React.Component {
  constructor() {
    super();
    this.state = {};
  }
  componentDidMount() {
    this.props.dispatch(fetchAllquizsets());
  }
  render() {
    let { quizsets } = this.props;
    console.log(quizsets);

    return (
      <>
        <div className="quizsets-list-wrapper">
          <h3 className="quizset-heading">
            Which type of question you start the quiz:
          </h3>
          <div className="quizsets-list">
            {quizsets &&
              quizsets.map(quizset => (
                <NavLink
                  className="quizset-text"
                  to={`/users/${localStorage.quizusername}/quizsets/${quizset.quizset}/${quizset._id}`}
                >
                  {quizset.quizset}
                </NavLink>
              ))}
          </div>
        </div>
      </>
    );
  }
}

function mapStateToProps(store) {
  console.log(store);
  return { quizsets: store.user.quizsets };
}

export default connect(mapStateToProps)(Quizset);
