import React from "react";
import { NavLink, withRouter } from "react-router-dom";

import "../styles/Quiz.css";

class Createquiz extends React.Component {
  constructor() {
    super();
    this.state = {
      title: "",
      option1: "",
      option2: "",
      option3: "",
      option4: "",
      answer: "",
      quizset: ""
    };
  }

  handleChange = event => {
    let { name, value } = event.target;
    this.setState({ [name]: value });
  };

  componentDidMount() {
    let id = this.props.match.params.id;
    fetch(`/api/v1/quizsets/${id}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      }
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) {
          this.setState({
            title: this.state.title,
            option1: this.state.option1,
            option2: this.state.option2,
            option3: this.state.option3,
            option4: this.state.option4,
            answer: this.state.answer,
            quizset: this.state.quizset
          });
        }
      });
  }

  handleChange = event => {
    let { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleSubmit = e => {
    e.preventDefault();
    let id = this.props.match.params.id;
    let quizset = this.props.match.params.quizset;

    fetch(`/api/v1/quizzes/${id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      },
      body: JSON.stringify({
        title: this.state.title,
        option1: this.state.option1,
        option2: this.state.option2,
        option3: this.state.option3,
        option4: this.state.option4,
        answer: this.state.answer
      })
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) {
          alert("successfully updated");
          this.props.history.push(
            `/admins/${localStorage.quizadminname}/quizsets`
          );
        }
      });
  };

  render() {
    return (
      <div className="sign-wrapper">
        <div className="sign-section">
          <form className="sign-form">
            Question: <br />
            <input
              className="sign-input first-input"
              type="text"
              name="title"
              placeholder="Enter Your Question"
              value={this.state.title}
              onChange={this.handleChange}
            />
            {/* // options // */}
            <div className="create-options">
              Options: <br />
              <input
                className="sign-input"
                type="text"
                name="option1"
                placeholder="First Option"
                value={this.state.option1}
                onChange={this.handleChange}
              />
              <input
                className="sign-input"
                type="text"
                name="option2"
                placeholder="Secound Option"
                value={this.state.option2}
                onChange={this.handleChange}
              />
              <input
                className="sign-input"
                type="text"
                name="option3"
                placeholder="Third Option"
                value={this.state.option3}
                onChange={this.handleChange}
              />
              <input
                className="sign-input"
                type="text"
                name="option4"
                placeholder="Fourth Option"
                value={this.state.option4}
                onChange={this.handleChange}
              />
              <br></br>
              Correct answer: <br />
              <select
                name="answer"
                className="sign-select"
                value={this.state.answer}
                onChange={this.handleChange}
              >
                <option value={this.state.option1}>{this.state.option1}</option>
                <option value={this.state.option2}>{this.state.option2}</option>
                <option value={this.state.option3}>{this.state.option3}</option>
                <option value={this.state.option4}>{this.state.option4}</option>
              </select>
              <br></br>
              Correct answer: <br />
              <p className="sign-quizsetname">
                {this.props.match.params.quizset}
              </p>
              <br></br>
              <input
                className="create-btn"
                type="submit"
                value="Submit"
                onClick={this.handleSubmit}
              />
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(Createquiz);
