import { GET_ADMIN, GET_QUIZSET } from "../types";

export function getadmin(payload) {
  return { type: GET_ADMIN, payload };
}

export function getAdmin() {
  return dispatch => {
    fetch("/api/v1/admin", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      }
    })
      .then(res => res.json())
      .then(data => {
        dispatch(getadmin(data.admin));
      });
  };
}

export function getQuizset(payload) {
  return {
    type: GET_QUIZSET,
    payload
  };
}

export function fetchQuizset(quizsetId) {
  console.log(quizsetId);
  return dispatch => {
    fetch(`/api/v1/quizsets/${quizsetId}`, {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      }
    })
      .then(res => res.json())
      .then(data => {
        console.log(data);
        dispatch(getQuizset(data.quizset));
      });
  };
}
