import React from "react";
import { withRouter, NavLink } from "react-router-dom";
import { connect } from "react-redux";
import { fetchQuizset } from "../../state/actions/admin.action";

import "../styles/Showquiz.css";

class Showquiz extends React.Component {
  constructor() {
    super();
    this.state = {
      show: false,
      quizset: "",
      filterqns: []
    };
  }

  handleQuestions = () => {
    this.props.dispatch(fetchQuizset(this.props.match.params.id));
  };
  componentDidMount() {
    this.handleQuestions();
  }

  handleDelete = id => {
    fetch(`/api/v1/quizzes/${id}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      }
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) {
          this.setState({ show: true });
          this.handleQuestions();
        }
      });
  };

  render() {
    let { quizset } = this.props;
    console.log(quizset);
    return (
      <div className="flex">
        {quizset &&
          quizset.questionsId.map((question, index) => (
            <div className="addquestion-questions">
              <div className="no-question">
                <p className="question-no-showquiz">{index + 1}.</p>
                <p className="question-title-showquiz">{question.title}</p>
                <button
                  onClick={() => this.handleDelete(question._id)}
                  className="question-no"
                >
                  Delete
                </button>
                <NavLink
                  to={`/admins/${localStorage.quizadminname}/quizsets/${quizset.quizset}/${question.title}/${question._id}/edit/`}
                  className="question-no"
                >
                  Edit
                </NavLink>
              </div>
              <div className="quizlist-card-wrapper">
                <button className="answers">{question.option1}</button>
                <button className="answers">{question.option2}</button>
                <button className="answers">{question.option3}</button>
                <button className="answers">{question.option4}</button>
                <button className="correct-answers">{question.answer}</button>
              </div>
            </div>
          ))}
        <div className="addquestion-btn">
          <NavLink
            className="button-header"
            to={`/admins/${localStorage.quizAdminName}/quizsets/${quizset.quizset}/${quizset._id}/createquiz`}
          >
            Add a question
          </NavLink>
        </div>
      </div>
    );
  }
}

function mapStateToProps(store) {
  console.log(store);
  return { quizset: store.admin.quizset };
}

export default connect(mapStateToProps)(withRouter(Showquiz));
