import React from "react";
import { Link } from "react-router-dom";
import validator from "validator";
import { withRouter } from "react-router-dom";

import "../styles/Signup.css";

class Signup extends React.Component {
  constructor() {
    super();
    this.state = {
      username: "",
      email: "",
      password: ""
    };
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value
    });
  };

  handleUserSignUp = event => {
    event.preventDefault();
    if (validator.isEmail(this.state.email)) {
      fetch("/api/v1/users", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          username: this.state.username,
          email: this.state.email,
          password: this.state.password
        })
      })
        .then(res => res.json())
        .then(data => {
          if (data.user) {
            this.props.history.push("/users/signin");
          } else {
            alert("enter valid details");
          }
        });
    } else {
      alert("Enter Valid Details");
    }
  };

  render() {
    return (
      <center>
        <div className="signup-div">
          <div className="rightsignup-section">
            <h1 className="userside-signupheader">Hello Friends!</h1>
            <p className="userside-signupheader">
              Already Have An Account! Go for Signin
            </p>
            <button className="signup-button">
              <Link className="span" to="/users/signin">
                Signin
              </Link>
            </button>
          </div>
          <div className="leftsignup-section">
            <h1 className="leftside-userheader"> Sign Up for User </h1>
            <form>
              <div className="input-icons">
                <i className="fas fa-user icon"></i>
                <input
                  className="input-username"
                  type="text"
                  name="username"
                  onChange={this.handleChange}
                  value={this.state.username}
                  placeholder="UserName"
                  required
                />
              </div>
              <div className="input-icons">
                <i className="fas fa-envelope icon"></i>

                <input
                  className="input-email"
                  type="email"
                  name="email"
                  value={this.state.email}
                  onChange={this.handleChange}
                  placeholder="Email"
                  required
                />
              </div>
              <div className="input-icons">
                <i className="fas fa-lock icon"></i>
                <input
                  className="input-password"
                  type="password"
                  name="password"
                  value={this.state.password}
                  onChange={this.handleChange}
                  placeholder="Password"
                  required
                />
              </div>
              <button onClick={this.handleUserSignUp} className="signup-button">
                SIGN UP
              </button>
            </form>
          </div>
        </div>
      </center>
    );
  }
}

export default withRouter(Signup);
