import { GET_USER, GET_ALLQUIZSETS, GET_QUIZSET } from "../types";

let initialstate = {
  user: "",
  quizsets: [],
  marks: "",
  questions: ""
};

export default function user(state = initialstate, action) {
  switch (action.type) {
    case GET_USER:
      return {
        ...state,
        userDetails: action.payload
      };
    case GET_ALLQUIZSETS:
      return {
        ...state,
        quizsets: action.payload
      };
    case GET_QUIZSET:
      return {
        ...state,
        questions: action.payload
      };
    default:
      return { ...state };
  }
}
