import React from "react";
import { NavLink, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { getAdmin } from "../../state/actions/admin.action";

import "../styles/Quiz.css";

class Quizset extends React.Component {
  constructor() {
    super();
    this.state = {
      quizsets: ""
    };
  }

  componentDidMount() {
    fetch("/api/v1/admin", {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      }
    })
      .then(res => res.json())
      .then(data => {
        console.log(data);
        if (data.success) {
          this.setState({ quizsets: data.admin.quizsetsId });
        }
      });
  }

  handleDelete = id => {
    fetch(`/api/v1/quizsets/${id}`, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizAdminToken
      }
    })
      .then(res => res.json())
      .then(data => {
        if (data.success) {
          alert("deleted successfully");
          this.props.dispatch(getAdmin());
        }
      });
  };

  render() {
    let { quizsets } = this.state;
    console.log(quizsets);

    return (
      <div className="sign-wrapper">
        <h1>All Quizsets</h1>
        {quizsets.length ? (
          quizsets &&
          quizsets.map(quizset => (
            <div className="addquestion-questions">
              <NavLink
                className="quizset-text-admin"
                to={`/admins/${localStorage.quizadminname}/quizsets/${quizset.quizset}/${quizset._id}/showquiz`}
              >
                {quizset.quizset}
              </NavLink>
              <button className="button-edit-delete">
                <NavLink
                  className="edit-btn"
                  to={`/admins/${localStorage.quizadminname}/quizsets/${quizset.quizset}/${quizset._id}/edit`}
                >
                  Edit
                </NavLink>
              </button>
              <button>
                <p
                  onClick={() => this.handleDelete(quizset._id)}
                  className="del-btn"
                >
                  Delete
                </p>
              </button>
            </div>
          ))
        ) : (
          <h3>No Quizset Found</h3>
        )}
      </div>
    );
  }
}

function mapStateToProps(store) {
  return { quizsets: store.admin.quizsets };
}

export default connect(mapStateToProps)(Quizset);
