import React from "react";
import { NavLink, withRouter } from "react-router-dom";

import "../styles/Quiz.css";

class EditQuizset extends React.Component {
  constructor() {
    super();
    this.state = {
      quizset: ""
    };
  }

  handleChange = event => {
    let { name, value } = event.target;
    this.setState({ [name]: value });
  };

  componentDidMount() {
    fetch(`/api/v1/quizsets/${this.props.match.params.id}`)
      .then(res => res.json())
      .then(data => {
        console.log(data);
        if (data.success) {
          this.setState({ quizset: data.quizset.quizset });
        }
      });
  }

  handleSubmit = e => {
    e.preventDefault();
    if (this.state.quizset) {
      fetch(`/api/v1/quizsets/${this.props.match.params.id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          Authorization: localStorage.quizAdminToken
        },
        body: JSON.stringify({
          quizset: this.state.quizset
        })
      })
        .then(res => res.json())
        .then(data => {
          console.log(data);
          if (data.success) {
            alert("Quizset is Successfully Edited");
            this.props.history.push(
              `/admins/${localStorage.quizadminname}/quizsets`
            );
          }
        });
    }
  };

  render() {
    return (
      <div className="sign-wrapper">
        <div className="sign-section">
          <form className="sign-form">
            Quizset: <br />
            <input
              className="sign-input first-input"
              type="text"
              name="quizset"
              placeholder="Enter Your Quizset"
              value={this.state.quizset}
              onChange={this.handleChange}
            />
            <input
              className="create-btn"
              type="submit"
              value="Submit"
              onClick={this.handleSubmit}
            />
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(EditQuizset);
