import React from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { fetchQuestions, fetchQuizset } from "../../state/actions/user.action";

import "../styles/Showquiz.css";

class Showquiz extends React.Component {
  constructor() {
    super();
    this.state = {
      attemptedQus: []
    };
  }

  componentDidMount() {
    this.props.dispatch(fetchQuizset(this.props.match.params.id));
  }

  handleAns = (event, _id, selectedOption) => {
    event.target.parentElement.parentElement.innerText = event.target.innerText;
    event.target.parentElement.style.visibility = "hidden";
    this.setState({
      attemptedQus: this.state.attemptedQus.concat({
        Id: _id,
        selectedOption
      })
    });
  };

  handleSubmit = event => {
    fetch(`/api/v1/user/quizsets/${this.props.questions._id}/submit`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: localStorage.quizuserToken
      },
      body: JSON.stringify({
        attemptedQus: this.state.attemptedQus
      })
    })
      .then(res => res.json())
      .then(data => {
        console.log(data);
        if (data.success) {
          this.props.history.push(`/users/${localStorage.quizusername}/marks`);
        }
      });
  };

  render() {
    let { questions } = this.props;
    console.log(questions);
    return (
      <div>
        <div className="quizlist-section-user">
          <p className="quizlist-heading">{questions && questions.quizset}</p>
          <p onClick={this.handleSubmit} className="quizlist-submit-btn">
            SUBMIT
          </p>
        </div>
        {questions && questions.questionsId.length ? (
          <div>
            {questions.questionsId.map((question, index) => (
              <div>
                <div className="noandquestion">
                  <div>
                    <p className="question-no-user">{index + 1}.</p>
                  </div>
                  <div>
                    <p className="question-title">{question.title}</p>
                  </div>
                </div>

                <div className="questions">
                  <div
                    className={`question-answers-user

                `}
                  >
                    <button
                      onClick={event => {
                        this.handleAns(event, question._id, question.option1);
                      }}
                      className="question-answers-item "
                    >
                      {question.option1}
                    </button>
                    <button
                      onClick={event => {
                        this.handleAns(event, question._id, question.option2);
                      }}
                      className="question-answers-item "
                    >
                      {question.option2}
                    </button>
                    <button
                      onClick={event => {
                        this.handleAns(event, question._id, question.option3);
                      }}
                      className="question-answers-item "
                    >
                      {question.option3}
                    </button>
                    <button
                      onClick={event => {
                        this.handleAns(event, question._id, question.option4);
                      }}
                      className="question-answers-item "
                    >
                      {question.option4}
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <h1>No Questions Found</h1>
        )}
      </div>
    );
  }
}

function mapStateToProps(store) {
  console.log(store.user.questions);
  return { questions: store.user.questions };
}

export default connect(mapStateToProps)(withRouter(Showquiz));
